namespace $ { export class $mpk_tss_reports_details extends $mol_view {

	/// report null
	report() {
		return null as any
	}

	/// title_prefix @ \Train
	title_prefix() {
		return this.$.$mol_locale.text( "$mpk_tss_reports_details_title_prefix" )
	}

	/// pages / <= Main
	pages() {
		return [].concat( this.Main() )
	}

	/// Main $mol_page
	/// 	minimal_width 400
	/// 	title <= title
	/// 	tools / <= Details_close
	/// 	body / <= Carriages
	@ $mol_mem
	Main() {
		return (( obj )=>{
			obj.minimal_width = () => 400
			obj.title = () => this.title()
			obj.tools = () => [].concat( this.Details_close() )
			obj.body = () => [].concat( this.Carriages() )
			return obj
		})( new this.$.$mol_page )
	}

	/// Details_close $mol_link
	/// 	arg * report null
	/// 	sub / <= Details_close_icon
	@ $mol_mem
	Details_close() {
		return (( obj )=>{
			obj.arg = () => ({
			"report" :  null as any ,
		})
			obj.sub = () => [].concat( this.Details_close_icon() )
			return obj
		})( new this.$.$mol_link )
	}

	/// Details_close_icon $mol_icon_cross
	@ $mol_mem
	Details_close_icon() {
		return (( obj )=>{
			return obj
		})( new this.$.$mol_icon_cross )
	}

	/// Carriages $mol_list rows <= carriage_links
	@ $mol_mem
	Carriages() {
		return (( obj )=>{
			obj.rows = () => this.carriage_links()
			return obj
		})( new this.$.$mol_list )
	}

	/// carriage_links /
	carriage_links() {
		return [] as any[]
	}

	/// Carriage_link!id $mpk_tss_reports_details_card carriage <= carriage!id
	@ $mol_mem_key
	Carriage_link( id : any ) {
		return (( obj )=>{
			obj.carriage = () => this.carriage(id)
			return obj
		})( new this.$.$mpk_tss_reports_details_card )
	}

	/// carriage!id null
	carriage( id : any ) {
		return null as any
	}

} }

