namespace $ { export class $mpk_tss_reports extends $mol_view {

	/// title @ \Reports
	title() {
		return this.$.$mol_locale.text( "$mpk_tss_reports_title" )
	}

	/// pages /
	/// 	<= Main
	/// 	<= Details_pages
	pages() {
		return [].concat( this.Main() , this.Details_pages() )
	}

	/// Main $mpk_tss_reports_page
	/// 	event_top?e <=> event_top?e
	/// 	tools <= tools
	/// 	title <= title
	/// 	filter_value?val <=> filter_value?val
	/// 	destructor <= clear_url
	/// 	body / <= Reports
	@ $mol_mem
	Main() {
		return (( obj )=>{
			obj.event_top = ( e? : any ) => this.event_top( e )
			obj.tools = () => this.tools()
			obj.title = () => this.title()
			obj.filter_value = ( val? : any ) => this.filter_value( val )
			obj.destructor = () => this.clear_url()
			obj.body = () => [].concat( this.Reports() )
			return obj
		})( new this.$.$mpk_tss_reports_page )
	}

	/// event_top?e null
	@ $mol_mem
	event_top( e? : any , force? : $mol_atom_force ) {
		return ( e !== void 0 ) ? e : null as any
	}

	/// tools /
	tools() {
		return [] as any[]
	}

	/// filter_value?val \
	@ $mol_mem
	filter_value( val? : any , force? : $mol_atom_force ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// clear_url null
	clear_url() {
		return null as any
	}

	/// Reports $mol_list rows <= report_links
	@ $mol_mem
	Reports() {
		return (( obj )=>{
			obj.rows = () => this.report_links()
			return obj
		})( new this.$.$mol_list )
	}

	/// report_links /
	report_links() {
		return [] as any[]
	}

	/// Details_pages null
	Details_pages() {
		return null as any
	}

	/// Report_link!id $mpk_tss_reports_card report <= report!id
	@ $mol_mem_key
	Report_link( id : any ) {
		return (( obj )=>{
			obj.report = () => this.report(id)
			return obj
		})( new this.$.$mpk_tss_reports_card )
	}

	/// report!id null
	report( id : any ) {
		return null as any
	}

	/// Report_details!id $mpk_tss_reports_details report <= report!id
	@ $mol_mem_key
	Report_details( id : any ) {
		return (( obj )=>{
			obj.report = () => this.report(id)
			return obj
		})( new this.$.$mpk_tss_reports_details )
	}

} }

namespace $ { export class $mpk_tss_reports_page extends $mol_page {

	/// minimal_width 200
	minimal_width() {
		return 200
	}

	/// sub /
	/// 	<= Head
	/// 	<= Filter_value
	/// 	<= Body
	sub() {
		return [].concat( this.Head() , this.Filter_value() , this.Body() )
	}

	/// Filter_value $mol_search query?val <=> filter_value?val
	@ $mol_mem
	Filter_value() {
		return (( obj )=>{
			obj.query = ( val? : any ) => this.filter_value( val )
			return obj
		})( new this.$.$mol_search )
	}

	/// filter_value?val \
	@ $mol_mem
	filter_value( val? : any , force? : $mol_atom_force ) {
		return ( val !== void 0 ) ? val : ""
	}

} }

