namespace $ { export class $mpk_tss_card extends $mol_link {

	/// minimal_height 32
	minimal_height() {
		return 32
	}

	/// sub / <= Card
	sub() {
		return [].concat( this.Card() )
	}

	/// Card $mol_card
	/// 	status <= status
	/// 	Status <= Footer
	/// 	Content <= Group
	@ $mol_mem
	Card() {
		return (( obj )=>{
			obj.status = () => this.status()
			obj.Status = () => this.Footer()
			obj.Content = () => this.Group()
			return obj
		})( new this.$.$mol_card )
	}

	/// status \
	status() {
		return ""
	}

	/// Footer $mol_view sub /
	/// 	<= Status_text
	/// 	<= Status_action
	@ $mol_mem
	Footer() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.Status_text() , this.Status_action() )
			return obj
		})( new this.$.$mol_view )
	}

	/// Status_text null
	Status_text() {
		return null as any
	}

	/// Status_action $mol_button_minor
	/// 	event_click?event <=> status_click?event
	/// 	sub / <= Status_icon
	@ $mol_mem
	Status_action() {
		return (( obj )=>{
			obj.event_click = ( event? : any ) => this.status_click( event )
			obj.sub = () => [].concat( this.Status_icon() )
			return obj
		})( new this.$.$mol_button_minor )
	}

	/// status_click?event null
	@ $mol_mem
	status_click( event? : any , force? : $mol_atom_force ) {
		return ( event !== void 0 ) ? event : null as any
	}

	/// Status_icon $mpk_tss_icon_download
	@ $mol_mem
	Status_icon() {
		return (( obj )=>{
			return obj
		})( new this.$.$mpk_tss_icon_download )
	}

	/// Group $mol_row sub / <= items
	@ $mol_mem
	Group() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.items() )
			return obj
		})( new this.$.$mol_row )
	}

	/// items /
	items() {
		return [] as any[]
	}

} }

