namespace $ { export class $mpk_tss_reports_status extends $mol_view {

	/// status null
	status() {
		return null as any
	}

	/// colors true
	colors() {
		return true
	}

	/// statuses *
	/// 	ready <= ready
	/// 	warning <= warning
	statuses() {
		return ({
			"ready" :  this.ready() ,
			"warning" :  this.warning() ,
		})
	}

	/// ready @ \Ready
	ready() {
		return this.$.$mol_locale.text( "$mpk_tss_reports_status_ready" )
	}

	/// warning @ \Warning
	warning() {
		return this.$.$mol_locale.text( "$mpk_tss_reports_status_warning" )
	}

	/// sub / <= status_text
	sub() {
		return [].concat( this.status_text() )
	}

	/// status_text \
	status_text() {
		return ""
	}

} }

