namespace $ { export class $mpk_tss extends $mol_view {

	/// sub /
	/// 	<= Head
	/// 	<= Body
	sub() {
		return [].concat( this.Head() , this.Body() )
	}

	/// Head $mol_view sub / <= Terminal_link
	@ $mol_mem
	Head() {
		return (( obj )=>{
			obj.sub = () => [].concat( this.Terminal_link() )
			return obj
		})( new this.$.$mol_view )
	}

	/// Terminal_link $mol_button_minor
	/// 	enabled false
	/// 	sub / <= terminal_label
	@ $mol_mem
	Terminal_link() {
		return (( obj )=>{
			obj.enabled = () => false
			obj.sub = () => [].concat( this.terminal_label() )
			return obj
		})( new this.$.$mol_button_minor )
	}

	/// terminal_label @ \Terminal  123
	terminal_label() {
		return this.$.$mol_locale.text( "$mpk_tss_terminal_label" )
	}

	/// Body $mpk_tss_main
	@ $mol_mem
	Body() {
		return (( obj )=>{
			return obj
		})( new this.$.$mpk_tss_main )
	}

} }

