namespace $ { export class $mpk_tss_pereferial_status extends $mol_view {

	/// status null
	status() {
		return null as any
	}

	/// colors true
	colors() {
		return true
	}

	/// statuses *
	/// 	ready <= ready
	/// 	error <= error
	/// 	not_responding <= not_responding
	/// 	inactive <= inactive
	/// 	unknown <= unknown
	statuses() {
		return ({
			"ready" :  this.ready() ,
			"error" :  this.error() ,
			"not_responding" :  this.not_responding() ,
			"inactive" :  this.inactive() ,
			"unknown" :  this.unknown() ,
		})
	}

	/// ready @ \Ready
	ready() {
		return this.$.$mol_locale.text( "$mpk_tss_pereferial_status_ready" )
	}

	/// error @ \Error
	error() {
		return this.$.$mol_locale.text( "$mpk_tss_pereferial_status_error" )
	}

	/// not_responding @ \Not responding
	not_responding() {
		return this.$.$mol_locale.text( "$mpk_tss_pereferial_status_not_responding" )
	}

	/// inactive @ \Inactive
	inactive() {
		return this.$.$mol_locale.text( "$mpk_tss_pereferial_status_inactive" )
	}

	/// unknown @ \Unknown
	unknown() {
		return this.$.$mol_locale.text( "$mpk_tss_pereferial_status_unknown" )
	}

	/// sub / <= status_text
	sub() {
		return [].concat( this.status_text() )
	}

	/// status_text <= unknown
	status_text() {
		return this.unknown()
	}

} }

