namespace $ { export class $mpk_tss_summary_status extends $mol_view {

	/// title \
	title() {
		return ""
	}

	/// statuses *
	/// 	warning <= warning
	/// 	error <= error
	/// 	success <= success
	/// 	ready <= ready
	statuses() {
		return ({
			"warning" :  this.warning() ,
			"error" :  this.error() ,
			"success" :  this.success() ,
			"ready" :  this.ready() ,
		})
	}

	/// warning @ \Warning
	warning() {
		return this.$.$mol_locale.text( "$mpk_tss_summary_status_warning" )
	}

	/// error @ \Error
	error() {
		return this.$.$mol_locale.text( "$mpk_tss_summary_status_error" )
	}

	/// success @ \Success
	success() {
		return this.$.$mol_locale.text( "$mpk_tss_summary_status_success" )
	}

	/// ready @ \Ready
	ready() {
		return this.$.$mol_locale.text( "$mpk_tss_summary_status_ready" )
	}

	/// sub / <= title
	sub() {
		return [].concat( this.title() )
	}

	/// attr *
	/// 	^
	/// 	mpk_tss_summary_status_type <= type
	attr() {
		return ({
			...super.attr() ,
			"mpk_tss_summary_status_type" :  this.type() ,
		})
	}

	/// type \success
	type() {
		return "success"
	}

} }

